  .auto-rs-power {
	background: #d7e5f01f;
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	padding: 16px;
	border-radius: 16px;
  }

  .auto-rs-power p {
	margin: 0;
  }

  .auto-rs-power div p:first-child {
	font-weight: 700;
	margin-bottom: 4px;
  }

  .auto-rs-power div p:last-child {
	font-size: 12px;
	color: #b1b1b2;
  }

  .vip-button {
	width: 80%;
	border: 0;
	background: #2a3038;
	padding: 12px 8px;
	border-radius: 100px;
	font-weight: 700;
	color: #ffffff;
	cursor: "pointer";
  }

  .button-wrapper {
	display: flex;
	flex-direction: column;
	gap: 2px;
  }

  .system-settings-button {
	display: flex;
	align-items: center;
	background-color: #2c2c2c;
	color: #fff;
	border: none;
	border-radius: 20px;
	padding: 8px 16px;
	font-size: 14px;
	cursor: pointer;
	transition: background-color 0.3s;
  }

  .system-settings-button .circle {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 18px;
	height: 18px;
	border-radius: 50%;
	border: 2px solid #fff;
	margin-right: 8px;
	font-size: 12px;
  }

  .system-settings-button.checked {
	background-color: #3b3b3b;
  }

  .system-settings-button.checked .circle {
	background-color: #6d86f0;
	border-color: #6d86f0;
	color: #fff;
  }

